.search-container {
  padding: 40px 20px;
  transition: all 0.3s ease;
}

.search-container.collapsed {
  padding: 20px;
}

.search-input {
  width: 80%;
  max-width: 600px;
  padding: 15px;
  font-size: 18px;
  border: 2px solid #ddd;
  border-radius: 25px;
  margin-right: 10px;
  outline: none;
  transition: border-color 0.3s ease;
}

.search-input:focus {
  border-color: #3498db;
}

.search-button {
  padding: 15px 30px;
  font-size: 18px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.search-button:hover {
  background-color: #2980b9;
}

.search-button:disabled,
.search-input:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}